@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body{
  font-family:"Roboto Condensed" sans-serif;
  margin:0;
  padding:0;
}
:root{
  --primary-white:#fff;
  --primary-orange:#f9ab00;
  --primary-dark: #100c08;
  --primary-red:#ef4035;
  --primary-hover-red:#f33501;
  --primary-grey:#343A40;
  --primary-carosal:#FF7F50;
}
nav{
  z-index:2;
}
/************************************Menu links***********************************************************/
nav a.nav-links{
  
  cursor:pointer;
  letter-spacing:0.0375rem;
}
nav ul li{
  font-size:0.8rem;
  text-transform:uppercase;
}
.navbar-nav > li > a{
  color:#fff!important;
  color:var(--primary-white)!important;
  margin-right:.75rem;
  margin-left:.75rem;
  border-top:.1875rem solid #343A40;
  border-top:.1875rem solid var(--primary-grey);
}
 .navbar-nav > li > a.active,.navbar-nav > li > a.active:focus{
   border-top:.1875rem solid #f9ab00;
   border-top:.1875rem solid var(--primary-orange);
 }
 .navbar-nav > li > a:hover{
  border-top:.1875rem solid #f9ab00;
  border-top:.1875rem solid var(--primary-orange);
 }

 /************************************Header***********************************************************/
 .header-wrapper{
   position:relative;
   background:url(/static/media/header.65f70ca8.jpg) no-repeat;
   background-size:cover;
   background-position: center;
   height:75vh; 
 }
 .main-info{
   display:flex;
   position:absolute;
   top:0;
   bottom: 0;
   left: 0;
   right: 0;
   flex-direction:column;
   justify-content:center;
   align-items:center;
   text-align:center;
   font-family:"Roboto Condensed",sans-serif;
   z-index: 1;
 }
 .main-info h1{
   color:#f9ab00;
   color:var(--primary-orange);
   text-transform:uppercase;
 }
 
 /********************************Typed Text**********************************************/
 .typed-text{
   font-size:2rem;
   color:#fff;
   color:var(--primary-white);
 }
 @media(max-width:768px){
   .main-info h1{
     font-size: 1.7rem !important;
   }
  .typed-text{
    font-size: 1.3rem !important;
  }
 }
 /********************************header main-btn-offer**********************************************/
 .btn-main-offer{
   border:1px solid #ef4035;
   border:1px solid var(--primary-red);
   border-radius:0;
   text-transform:uppercase;
   padding:.625rem .75rem .75rem .75rem;
   color:#fff;
   color:var(--primary-white );
   margin:2rem;
}
.btn-main-offer:hover{
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color:#fff;
  color:var(--primary-white );
  transition:.2s ease-in-out;
}
 /********************************Particles js**********************************************/
 .tsparticles-canvas-el{
   position:absolute;
   height:70vh;
   width:100vw;
   z-index:1;
 }
 @media(max-width:768px){
   .particles-canvas{
     display:none;
   }
 }
 /***********************************About Me******************************************************/
 .main-container{
   width:100vw;
   height:auto;
   background:#FF7F50 !important;
   background:var(--primary-carosal) !important;
   align-items:center;
   position:relative;
 }
 .main-container::after{
    background-image: url(/static/media/wave-bottom.a962c87b.svg);
    position:absolute;
    content:"";
    bottom:0;
    width:110%;
    left:-77px;
    height:14rem;
 }

 
 .main-heading{
   font-weight:bold;
   font-size:3rem;
   color:#fff;
   color:var(--primary-white);
   text-transform: uppercase;
   text-align:center;
   padding-top:110px;
   margin-bottom:0;
 }
 .about-container{
   
   width:100%;
   height:auto;
   margin-top:0 ;
   margin-bottom: 0;
   display:flex;
   align-items:center;
   
 }
 .col1{
  
    margin-bottom: 20px;
    margin-left:20px;
    margin-right:20px;
    width: auto;
    padding:35px;
 }

 .content-about{
  text-align: center;
  padding: 10px;
  font-size: 1px;
  
 }
 .introcontent{
  color:#fff !important;
  color:var(--primary-white) !important;
  margin-top: 0;
  margin-bottom: 0;
  margin-left:20px;
  margin-right:20px;
  line-height: 1.5rem;
  text-align: justify;
  

 }
 .intro{
   font-weight:bold !important;
   font-size: 2rem;
   color:#fff !important;
   color:var(--primary-white) !important;
   margin-top: 0;
   margin-bottom: 0;
   margin-left:20px;
   margin-right:20px;
   
 }
 .col2{
  
  width:50%;
  text-align: center;
  padding:70px;
  border-radius:50% 0 0 50%;
  z-index:1;
  
 }
 .programmer-img{
   border-radius:50%;
   width: 350px;
 }
 @media(max-width:768px){
   .main-container::after{
     display:none;
   }
   .main-heading{
     font-size:1.5rem;
   }
   .about-container{
     display:inline-block;
   }
   .intro{
     font-size: 1rem;
   }
   .col1{
     padding-bottom:4px;
     margin-bottom:2px;
   }
   .col2{
     width:100%;
     padding:20px;
   }
   .programmer-img{
    width: 250px;
   }
 }
 /*************************Skill Page Content********************************/
 .skill-main-container{
   position: relative;
   width:100%;
   height:auto;
   background:#343A40;
   background:var(--primary-grey);
   display:flex;
   justify-content:center;
   align-items:center;
 }
 .skill-content{
   position:relative;
   max-width:900px;
 }
.skill-content h1{
  margin:0;
  padding:0;
  font-size:3rem;
  color:#fff;
  color:var(--primary-white) ;
  font-weight:bold;
  margin:20px;
  text-align: center;
  padding:30px;
}
.skill-content p{
    color:#fff;
    color:var(--primary-white) ; 
    margin-left: 30px;
    margin-right: 30px;
}
/***********************************skill-shape**********************************************/
.shape-wrapper{
    position: relative;
    width: 100%;
    height: auto;
    background: #343A40;
    background: var(--primary-grey);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 65px;
    margin-bottom: 60px;
}
@media(max-width:768px){

.shape-wrapper{
  flex-direction: column;
}
.skill-content h1{
  font-size: 1.5rem;
}
.shape-container:hover:before{
 
  display:none !important;
  
}
.shape-container:hover:after{
 
  display:none;
  
}


}
.shape-container{
  width:120px;
  height:120px;
  top:350px;
  left:50%;
  -webkit-transform:transalte(-50%,-50%);
          transform:transalte(-50%,-50%);
  background:transparent;
  border:10px solid transparent;
  padding:10px;
  box-sizing: border-box;
  transition: .5s;
  box-shadow:none;
}
.shape-container:before{
  content:"";
  position:absolute;
  width:10px;
  height:90%;
  background:#FF7F50;
  left:50px;
  top:5%;
  transition: .5s;
  opacity:0;
  -webkit-animation-delay:.3s;
          animation-delay:.3s;
  z-index:1;
}
.shape-container:after{
  content:"";
  position:absolute;
  width:10px;
  height:90%;
  background:#FF7F50;
  right:50px;
  top:5%;
  transition: .5s;
  opacity:0;
  -webkit-animation-delay:.3s;
          animation-delay:.3s;
  z-index:1;
}
.shape-container:hover:before{
 left:-32px;
 opacity:1;
}
.shape-container:hover:after{
  right:-32px;
  opacity:1;
 }
 .shape-container:hover .shape{
   border-radius:0;
 }
 .shape-container:hover{
   background-color:#343A40;
   background-color:var(--primary-grey) ;
   border:10px solid #FF7F50;
   border:10px solid var(--primary-carosal);
 }
.shape {
  width:100%;
  height:100%;
  background:#343A40;
  background:var(--primary-grey);
  box-sizing:border-box;
  -webkit-transform:rotate(45deg) scale(1.2);
          transform:rotate(45deg) scale(1.2);
  border:8px solid #FF7F50;
  border:8px solid var(--primary-carosal);
  border-radius:50%;
  transition:.5s;
  box-shadow:0 0 15px rgba(0 0 0,.5)
}
.shape p{
  margin:0;
  width: 135%;
  position: fixed;
  padding-top: 3px;
  text-align:center;
  box-sizing:border-box;
  transition:.5s;
  -webkit-transform:rotate(-45deg) translate(-12px,-3px);
          transform:rotate(-45deg) translate(-12px,-3px);
  color:#100c08;
  color:var(--primary-dark);

}
.ticon{
    width: 57px;
    margin-right: 7px;
    margin-top: 4px;
}

